<template>
  <div>
    <div class="mainTitle"></div>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="6">
          <a-input-search v-model="queryParam.username" placeholder="" enter-button allowClear @search="getUserList"/>
        </a-col>
        <a-col :span="4">
          <a-button type="primary" @click="addUserVisible = true"></a-button>
        </a-col>
      </a-row>
      <a-table rowKey="username" :columns="columns" :pagination="pagination" :dataSource="userList" @change="handleTableChange" bordered style="margin-top:40px;">
        <a-tag slot="role" slot-scope="role" :color="role===1?'palevioletred':'deepskyblue'">{{ role === 1 ? "" : ""}}</a-tag>
        <template slot="action" slot-scope="data">
        <div class="actionSlot">
          <a-button type="primary" icon="edit" @click="editUser(data.ID)" ></a-button>
          <a-button type="danger" icon="delete" @click="deleteUser(data.ID)" style="margin:0 20px;"></a-button>
          <a-button type="info" icon="redo" @click="editPsw(data.ID)" ></a-button>
        </div>
        </template>
      </a-table>
    </a-card>

    <!---->
    <a-modal
      title=""
      :visible="addUserVisible"
      @ok="addUserOk"
      @cancel="addUserCancel"
    destroyOnClose >
      <a-form-model :model="newUser" :rules="newUserRule" ref="addUserRef">
        <a-form-model-item has-feedback label="" prop="username">
          <a-input v-model="newUser.username"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password v-model="newUser.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkpsw">
          <a-input-password v-model="newUser.checkpsw"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!---->
    <a-modal
        title=""
        :visible="editUserVisible"
        @ok="editUserOk"
        @cancel="editUserCancel"
        destroyOnClose >
      <a-form-model :model="userInfo" :rules="userRule" ref="editUserRef">
        <a-form-model-item has-feedback label="" prop="username">
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item label="" prop="role">
          <a-select placeholder="" @change="adminChange">
            <a-select-option key="1" value="1"></a-select-option>
            <a-select-option key="2" value="2"></a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!---->
    <a-modal
        title=""
        :visible="editPswVisible"
        @ok="editPswOk"
        @cancel="editPswCancel"
        destroyOnClose >
      <a-form-model :model="newPsw" :rules="newPswRule" ref="editPswRef">
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password v-model="newPsw.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkpsw">
          <a-input-password v-model="newPsw.checkpsw"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  {
    title:'ID',
    dataIndex:'ID',
    width:'10%',
    key:'id',
    align:"center",
  },
  {
    title:"",
    dataIndex:"username",
    width:"20%",
    key:"username",
    align:"center",
  },
  {
    title:"",
    dataIndex:"role",
    width:"20%",
    key:"role",
    align:"center",
    scopedSlots:{customRender:'role'},
  },
  {
    title:"",
    width:"30%",
    key:"action",
    align:"center",
    scopedSlots:{customRender:'action'},
  },
]

export default {
  data(){
    return{
      pagination:{
        pageSizeOptions: ['5','10','20'],
        pageSize:5,
        total:0,
        showSizeChanger:true,
        showTotal:(total)=>`${total}`,
      },
      userInfo:{
        id:0,
        username:'',
        role:2,
      },
      newUser:{
        id:0,
        username:'',
        password:'',
        checkpsw:'',
      },
      newPsw:{
        id:0,
        password:'',
        checkpsw:'',
      },
      userList:[],
      columns,
      queryParam:{
        username:'',
        pagesize:5,
        pagenum:1,
      },
      visible:false,
      addUserVisible:false,
      editUserVisible:false,
      editPswVisible:false,
      userRule:{
        username:[{validator:(rule,value,callback)=>{
          if(this.userInfo.username === ""){
            callback(new Error(""))
          }
            if([...this.userInfo.username].length < 4 || [...this.userInfo.username].length > 12){
              callback(new Error("  4  12 "))
            }else{
              callback()
            }
          },trigger:"blur"}],
      },
      newUserRule:{
        username:[{validator:(rule,value,callback)=>{
            if(this.newUser.username === ""){
              callback(new Error(""))
            }
            if([...this.newUser.username].length < 4 || [...this.newUser.username].length > 12){
              callback(new Error("  4  12 "))
            }else{
              callback()
            }
          },trigger:"blur"}],
        password:[{validator:(rule,value,callback)=>{
            if(this.newUser.password === ""){
              callback(new Error(''))
            }
            if([...this.newUser.password].length < 6 || [...this.newUser.password].length > 20){
              callback(new Error(" 6  20 "))
            }else{
              callback()
            }
          },trigger:'blur',
        }],
        checkpsw:[{validator:(rule,value,callback)=>{
            if(this.newUser.checkpsw === ""){
              callback(new Error(''))
            }
            if(this.newUser.checkpsw !== this.newUser.password){
              callback(new Error(''))
            }else{
              callback()
            }
          },trigger:'blur'}]
      },
      newPswRule:{
        password:[{validator:(rule,value,callback)=>{
            if(this.newPsw.password === ""){
              callback(new Error(''))
            }
            if([...this.newPsw.password].length < 6 || [...this.newPsw.password].length > 20){
              callback(new Error(" 6  20 "))
            }else{
              callback()
            }
          },trigger:'blur',
        }],
        checkpsw:[{validator:(rule,value,callback)=>{
            if(this.newPsw.checkpsw === ""){
              callback(new Error(''))
            }
            if(this.newPsw.checkpsw !== this.newPsw.password){
              callback(new Error(''))
            }else{
              callback()
            }
          },trigger:'blur'}]
      },
    }
  },
  created(){
    this.getUserList()
  },
  methods:{
    async getUserList(){
      const { data : res } = await this.$http.get("users", {
        params: {
          username:this.queryParam.username,
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        }
      })
      if(res.status !== 200) return this.$message.error(res.message)
      this.userList = res.data
      this.pagination.total = res.total
    },

    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pagesize = pagination.pageSize
      this.queryParam.pagenum = pagination.current

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getUserList()
    },

    async deleteUser(id){
      this.$confirm({
        title:"",
        content:"",
        onOk:async()=>{
          const res = await this.$http.delete(`user/${id}`)
          if(res.status !== 200) return this.$message.error(res.message)
          this.getUserList()
          this.$message.success("")
        },
        onCancel:()=>{
          this.$message.info("")
        },
      })
    },

    addUserOk(){
      this.$refs.addUserRef.validate(async (valid)=>{
        if(!valid)return this.$message.error("")
        const { data:res } = await this.$http.post('user/add', {
          username: this.newUser.username,
          password: this.newUser.password,
          role:2,
        })
        if(res.status !== 200) return this.$message.error(res.message)
        this.$message.success("")
        this.addUserCancel()
        this.getUserList()
      })
    },

    addUserCancel(){
      this.$refs.addUserRef.resetFields()
      this.addUserVisible = false
    },
    adminChange(value){
      this.userInfo.role = Number(value)
    },

    async editUser(id){
      this.editUserVisible = true
      const {data:res} = await this.$http.get(`user/${id}`)
      this.userInfo = res.data
      this.userInfo.id = id
    },
    editUserOk(){
      this.$refs.editUserRef.validate(async (valid)=>{
        if(!valid)return this.$message.error("")
        if(this.userInfo.role == 0)this.userInfo.role = 2
        const { data:res } = await this.$http.put('user/'+ this.userInfo.id, {
          username: this.userInfo.username,
          role: this.userInfo.role,
        })
        if(res.status !== 200) return this.$message.error(res.message)
        this.$message.success("")
        this.editUserCancel()
        this.getUserList()
      })
    },
    editUserCancel(){
        this.$refs.editUserRef.resetFields()
        this.editUserVisible = false
    },

    async editPsw(id){
      this.editPswVisible = true
      this.newPsw.id = id
    },
    editPswOk(){
      this.$refs.editPswRef.validate(async (valid)=>{
        if(!valid)return this.$message.error("")
        const { data:res } = await this.$http.put('user/pwd/'+ this.newPsw.id, {
          password: this.newPsw.password,
        })
        if(res.status !== 200) return this.$message.error(res.message)
        this.$message.success("")
        this.editPswCancel()
        this.getUserList()
      })
    },
    editPswCancel(){
      this.$refs.editPswRef.resetFields()
      this.editPswVisible = false
    },
  }
}
</script>
<style scoped>
.mainTitle{
  font-size: calc(20px + 2vw);
  text-align: center;
  align-content: center;
  background-color: white;
  font-family:"",sans-serif;
  user-select: none;
}
</style>
