<template>
  <div>
    <div class="mainTitle"></div>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="4">
          <a-button type="primary" @click="addFriendLinkVisible = true"></a-button>
        </a-col>
      </a-row>
      <a-table rowKey="name" :columns="columns" :pagination="pagination" :dataSource="FriendList" @change="handleTableChange" bordered style="margin-top:40px;">
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" icon="delete" @click="deleteFriend(data.id)" style="margin:0 10px;"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>

    <!---->
    <a-modal
        title=""
        :visible="addFriendLinkVisible"
        @ok="addFriendOk"
        @cancel="addFriendCancel"
        destroyOnClose >
      <a-form-model :model="newFriend" :rules="newFriendRule" ref="addFriendRef">
        <a-form-model-item has-feedback label="" prop="name">
          <a-input v-model="newFriend.name"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="link">
          <a-input v-model="newFriend.link"></a-input>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>
<script>
const columns = [
  {
    title:'ID',
    dataIndex:'id',
    width:'10%',
    key:'id',
    align:"center",
  },
  {
    title:"",
    dataIndex:"name",
    width:"20%",
    key:"name",
    align:"center",
  },
  {
    title:"",
    dataIndex:"link",
    width:"20%",
    key:"link",
    align:"center",
  },
  {
    title:"",
    width:"30%",
    key:"action",
    align:"center",
    scopedSlots:{customRender:'action'},
  },
]
export default {
  data(){
    return{
      pagination:{
        pageSizeOptions: ['5','10','20'],
        pageSize:5,
        total:0,
        showSizeChanger:true,
        showTotal:(total)=>`${total}`,
      },
      queryParam:{
        pagesize:5,
        pagenum:1,
      },
      FriendList:[],
      columns,
      newFriend:{
        name:'',
        link:'',
      },
      visible:false,
      addFriendLinkVisible:false,
      newFriendRule:{
        name:[{validator:(rule,value,callback)=>{
            if(this.newFriend.name === ""){
              callback(new Error(""))
            }
            callback()
          },trigger:"blur"}],
        link:[{validator:(rule,value,callback)=>{
            if(this.newFriend.link === ""){
              callback(new Error(""))
            }
            callback()
          },trigger:"blur"}],
      },
    }
  },
  created(){
    this.getFriendList()
  },

  methods:{
    async getFriendList(){
      const { data : res } = await this.$http.get("friend", {
        params: {
          pagesize:this.queryParam.pagesize,
          pagenum:this.queryParam.pagenum,
        }
      })
      if(res.status !== 200) return this.$message.error(res.message)
      this.FriendList= res.data
      this.pagination.total = res.total
    },

    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pagesize = pagination.pageSize
      this.queryParam.pagenum = pagination.current

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getFriendList()
    },

    async deleteFriend(id){
      this.$confirm({
        title:"",
        content:"",
        onOk:async()=>{
          const res = await this.$http.delete(`friend/${id}`)
          if(res.data.status !== 200) {
            this.$message.error("")
          }else{
            this.$message.success("")
          }
          this.getFriendList()
        },
        onCancel:()=>{
          this.$message.info("")
        },
      })
    },


    addFriendOk(){
      this.$refs.addFriendRef.validate(async (valid)=>{
        if(!valid)return this.$message.error("")
        const { data:res } = await this.$http.post('friend/add', {
          name: this.newFriend.name,
          link:this.newFriend.link
        })
        if(res.status !== 200) return this.$message.error(res.message)
        this.$message.success("")
        this.addFriendCancel()
        this.getFriendList()
      })
    },

    addFriendCancel(){
      this.$refs.addFriendRef.resetFields()
      this.addFriendLinkVisible = false
    },
  }
}
</script>

<style scoped>
.mainTitle{
  font-size: calc(20px + 2vw);
  text-align: center;
  align-content: center;
  background-color: white;
  font-family:"",sans-serif;
  user-select: none;
}
</style>
